.nav {
    width: 600px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    a {
        color: #05fdd8;
        text-decoration: none;
        &.active {
            color: #FF6D05;
        }
        &:hover {
            color: #BF6D33;
            text-decoration: underline;
            line-height: 1;
            cursor: pointer;
        }
        &:last-child {
            border: 1px solid #05fdd8;
            border-radius: 6px;
            padding: 10px 22px;
            color: #05fdd8;
            background-color: #172523;
            transition-property: color,border-color,background-color,transform,visibility,opacity,font-size;
            transition-duration: .2s;
            transition-timing-function: ease;
        }

    }

}

@media screen and (max-width: 804px) {
    .nav{display: none}
}


